"use client"

import { useState } from "react";
import { motion } from "framer-motion";
import staffData from "./organigramme.json";
import { section } from "framer-motion/client";

function StaffBubble({ person }:any) {
  const [flipped, setFlipped] = useState(false);

  return (
    <motion.div
      className="w-40 h-40 rounded-full shadow-xl cursor-pointer relative"
      onClick={() => setFlipped(!flipped)}
      whileHover={{ scale: 1.05 }}
    >
      {flipped ? (
        <div className="absolute inset-0 flex flex-col items-center justify-center bg-blue-600 text-white rounded-full p-2 text-center">
          <p className="font-bold">{person.name}</p>
          <p className="text-sm">{person.role}</p>
          <p className="text-xs">{person.email}</p>
        </div>
      ) : (
        <div className="absolute inset-0 flex flex-col items-center justify-center bg-gray-100 rounded-full overflow-hidden">
          <img src={person.image} alt={person.name} className="w-full h-full object-cover rounded-full" />
        </div>
      )}
    </motion.div>
  );
}

export default function Organigramme() {
  return (
    <section>
                
    </section>
  );
}
